/* src/App.css */
.app {
  text-align: center;
  /* background-image: url('a.png');  */
  background: linear-gradient(to right, #9e60f3, #3a4af2); /* Example gradient colors */
  background-size: cover; /* Ensures the image covers the entire background */
  font-family: Arial, sans-serif;
  margin: 0;
}

.video-player {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  border-radius: 10px;
  text-align: center;
}
#video-section {
  margin-bottom: 20px;
}

@keyframes popIn {
  0% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
.news-player {
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  border-radius: 10px;
  transition: transform 0.3s;
  animation: popIn 0.8s ease; /* Added animation */
}

.tv-news-ui {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 10px;
}

.tv-channels {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
}

.news-channel-img {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #fff;
}

.channel-name {
  font-size: 1.2em;
  margin-top: 10px;
}

.channel-name {
  font-size: 1.2em;
  margin-top: 10px;
  text-align: center;
  color: #333;
}

/* Add this to NewsPlayer.css */
.channel-button {
  background-color: #004080;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  font-size: 1.2em;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  display: block;
  margin: 0 auto;
}


.channel-button:hover {
  background-color: #002d59;
}

.news-player:hover {
  transform: scale(1.05);
}
.channel-button:hover {
  background-color: #002d59;
}
.tv-news-ui {
  text-align: center;
  padding: 20px;
}
.tv-channels {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}