/* Carousel styles */
.carousel-container {
	margin-top: 20px;
	max-height: 300px; /* Adjust the maximum height as needed */
  }
  
  .carousel-container .carousel .slider-wrapper {
	border-radius: 5px;
	overflow: hidden;
	max-height: 300px; /* Adjust the maximum height as needed */
  }
  
  .carousel-container .carousel .slider-wrapper .slider {
	transition: transform 0.5s ease;
  }
  
  .carousel-container .carousel .slider-wrapper .slider .slide {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #ccc;
  }
  
  .carousel-container .carousel .slider-wrapper .slider .slide img {
	max-height: 100%;
	width: auto;
	margin: 0 auto;
  }

  .live-tv-btn {
	padding : 10px 15px;
	border-radius: 10px;
	font-weight: bold;
	background-color : white;
	cursor: pointer;
  }


  .red-dot {
	border-radius : 10px;
	height : 10px;
	width : 10px;
	background-color : red;
	display: inline-block;
  }

  .dot-space {
	display: inline-block;
	width : 10px;
  }

  .account-icon {
	height : 30px;
	width : 30px;
  }
